import { Roborock } from "../main";

const { createCanvas, loadImage } = require("@napi-rs/canvas");
const { Image } = require("@napi-rs/canvas");

// Farben ändern
const colors = {
	floor: "#23465e",
	obstacle: "#2b2e30",
	path: "rgba(255,255,255,0.5)",
	newmap: true,
};
const orgcolors = [
	"#C05A41", // 0
	"#4579B5", // 1
	"#017E82", // 2
	"#BD7B00", // 3
	"#434242", // 4 wall
	"#dfdfdf", // 5 deselected floor
];
const offset = 60;
const MATRIX_SIZE = 32; // 32x32 grid

const robot =
	"data:image/png;base64,";
const robot1 =
	"data:image/png;base64,";
const charger =
	"data:image/png;base64,";
const tank =
	"data:image/png;base64,";
const spaceship =
	"data:image/png;base64,";
const robot2 =
	"data:image/png;base64,";
const originalRobot =
	"data:image/png;base64,";
const go_to_pin =
	"data:image/png;base64,";

//const ctximg = canvasImg.getContext('2d');

let img = new Image(); // Create a new Image

let img_charger = new Image();

const obstacleTitles = {
	0: "Wire",
	1: "Pet waste",
	2: "Footwear",
	3: "Pedestal",
	4: "Pedestal",
	5: "Power strip",
	9: "Scale",
	10: "Fabric",
	18: "Dustpan",
	25: "Dustpan",
	26: "Bar",
	27: "Bar",
};

interface CanvasMapOptions {
	selectedMap?: any;
	mappedRooms?: any;
	options?: any;
}

export class MapCreator {
	adapter: Roborock;
	adjacencyMatrix: number[][];

	constructor(adapter: Roborock) {
		this.adapter = adapter;
		this.adjacencyMatrix = new Array(1024).fill(0); // Matrix, initial leer
	}

	getX(dimensions, px) {
		return (px * this.adapter.config.map_scale) % dimensions.width;
	}
	getY(dimensions, px) {
		return dimensions.height - Math.floor(px / (dimensions.width / this.adapter.config.map_scale)) * this.adapter.config.map_scale - this.adapter.config.map_scale;
	}

	robotXtoPixelX(image, robotCoord) {
		return (robotCoord - image.position.left) * this.adapter.config.map_scale + 1;
	}
	robotYtoPixelY(image, robotCoord) {
		return (image.dimensions.height / this.adapter.config.map_scale + image.position.top - robotCoord) * this.adapter.config.map_scale - 1;
	}

	rotateCanvas(img, angleInDegrees) {
		const canvasImg = createCanvas(img.width, img.height);
		const ctx = canvasImg.getContext("2d");
		const angleOffset = 90;
		let angleInRadians = ((angleInDegrees - angleOffset) * Math.PI) / 180;
		angleInRadians = ((angleInRadians + Math.PI) % (2 * Math.PI)) - Math.PI; // Normalize angle to -π to π

		ctx.translate(img.width / 2, img.height / 2);
		ctx.rotate(-angleInRadians);
		ctx.translate(-img.width / 2, -img.height / 2);
		ctx.drawImage(img, 0, 0);

		return canvasImg;
	}

	drawLineBresenham(imageData, x1, y1, x2, y2) {
		const pixels = imageData.data;

		const dx = Math.abs(x2 - x1);
		const dy = Math.abs(y2 - y1);
		const sx = x1 < x2 ? 1 : -1;
		const sy = y1 < y2 ? 1 : -1;
		let err = dx - dy;

		for (;;) {
			// Setze Pixel im ImageData
			if (x1 >= 0 && x1 < imageData.width && y1 >= 0 && y1 < imageData.height) {
				// handle out of bounds. lineto would already do this but we need to set pixels directly
				const index = (x1 + y1 * imageData.width) * 4;
				pixels[index] = 128; // r
				pixels[index + 1] = 128; // g
				pixels[index + 2] = 128; // b
				pixels[index + 3] = 128; // a
			}

			if (x1 === x2 && y1 === y2) break;
			const e2 = 2 * err;
			if (e2 > -dy) {
				err -= dy;
				x1 += sx;
			}
			if (e2 < dx) {
				err += dx;
				y1 += sy;
			}
		}
	}

	/**
	 * Builds an adjacency matrix indicating which segments (rooms) touch each other.
	 * The matrix is 32x32 because segment IDs range from 0 to 31.
	 * It only checks neighbors above and to the left to avoid double counting.
	 *
	 * @param {number[]} segmentPixels - Array of pixels with embedded segment IDs.
	 * @param {number} width - Width of the map in pixels.
	 * @param {number} height - Height of the map in pixels.
	 * @returns {number[][]} - A 32x32 matrix where matrix[a][b] === 1 means segment a touches segment b.
	 */
	buildAdjacencyMatrix(segmentPixels, width, height) {
		// Initialize 32x32 matrix filled with zeros
		const matrix = Array.from({ length: MATRIX_SIZE }, () => Array(MATRIX_SIZE).fill(0));
		// Create a map from pixel index to segment number, default -1 (no segment)
		const segMap = new Array(width * height).fill(-1);

		// Fill segMap with segment numbers extracted from pixels
		for (const px of segmentPixels) {
			const pixelIndex = px & 0x1fffff; // pixel index (lower 21 bits)
			const segnum = (px >> 21) & 0x1f; // segment id (next 5 bits)
			segMap[pixelIndex] = segnum;
		}

		// Check only the pixel above and to the left for adjacency
		for (let i = 0; i < segMap.length; i++) {
			const segA = segMap[i];
			if (segA < 0) continue; // no segment here
			const x = i % width;
			const y = Math.floor(i / width);

			// Check pixel above
			if (y > 0) {
				const idxTop = i - width;
				const segB = segMap[idxTop];
				if (segB >= 0 && segA !== segB) {
					matrix[segA][segB] = 1;
					matrix[segB][segA] = 1; // adjacency is symmetric
				}
			}

			// Check pixel to the left
			if (x > 0) {
				const idxLeft = i - 1;
				const segB = segMap[idxLeft];
				if (segB >= 0 && segA !== segB) {
					matrix[segA][segB] = 1;
					matrix[segB][segA] = 1;
				}
			}
		}

		return matrix;
	}

	/**
	 * Returns the room name for a given segment number by looking up mappings.
	 *
	 * @param {number|string} segnum           The segment number to find the room name for.
	 * @param {Array} mappedRooms               Array of mappedRooms entries, e.g., [[segmentNum, "roomIdString", mappedIndex], ...]
	 * @param {Array} roomIDs                   Array of room objects, e.g., [{id: number, name: string}, ...]
	 * @returns {string}                       The found room name, or "Room X" as a fallback.
	 */
	getRoomNameBySegment(segnum, mappedRooms, roomIDs) {
		if (!Array.isArray(mappedRooms) || !Array.isArray(roomIDs)) return "Room " + segnum;

		// Find the mapping entry for the given segment number
		const mapping = mappedRooms.find((entry) => entry[0] == segnum);
		if (!mapping) return "Room " + segnum;

		const [, roomIdString, mappedIndex] = mapping;
		const numId = Number(roomIdString);

		// 1. Try to find room by roomId
		const room = roomIDs.find((r) => r.id === numId);
		if (room && room.name) return room.name;

		// 2. Try to find room by mapped index (0-based)
		if (typeof mappedIndex === "number") {
			if (roomIDs[mappedIndex] && roomIDs[mappedIndex].name) return roomIDs[mappedIndex].name;
			if (roomIDs[mappedIndex - 1] && roomIDs[mappedIndex - 1].name) return roomIDs[mappedIndex - 1].name;
		}

		return "Room " + segnum;
	}

	hexToRGBA(hex, alpha = 1) {
		const r = parseInt(hex.slice(1, 3), 16);
		const g = parseInt(hex.slice(3, 5), 16);
		const b = parseInt(hex.slice(5, 7), 16);
		return `rgba(${r}, ${g}, ${b}, ${alpha})`;
	}

	async canvasMap(mapdata: any, params: CanvasMapOptions = {}) {
		const {
			selectedMap = null,
			mappedRooms = null,
			options = {},
		} = params;

		if (options) {
			colors.floor = options.FLOORCOLOR;
			colors.obstacle = options.WALLCOLOR;
			colors.path = options.PATHCOLOR;
			colors.newmap = options && options.newmap ? options.newmap : true;
			if (options.ROBOT === "robot") {
				img = await loadImage(robot);
			} else if (options.ROBOT === "robot1") {
				img = await loadImage(robot1);
			} else if (options.ROBOT === "tank") {
				img = await loadImage(tank);
			} else if (options.ROBOT === "spaceship") {
				img = await loadImage(spaceship);
			} else if (options.ROBOT === "robot2") {
				img = await loadImage(robot2);
			} else if (options.ROBOT === "originalRobot") {
				img = await loadImage(originalRobot);
			}
		} else {
			img = await loadImage(originalRobot);
		}
		img_charger = await loadImage(charger);

		let maxtop = 0;
		let maxleft = 0;
		let minleft = 0;
		let mintop = 0;

		mapdata.IMAGE.dimensions.width = mapdata.IMAGE.dimensions.width * this.adapter.config.map_scale;
		mapdata.IMAGE.dimensions.height = mapdata.IMAGE.dimensions.height * this.adapter.config.map_scale;

		if (mapdata.IMAGE.dimensions.height > 0 && mapdata.IMAGE.dimensions.width > 0) {
			const canvas = createCanvas(mapdata.IMAGE.dimensions.width, mapdata.IMAGE.dimensions.height);
			const ctx = canvas.getContext("2d");

			if (mapdata.IMAGE.pixels.floor && mapdata.IMAGE.pixels.floor.length !== 0) {
				if (typeof mapdata.IMAGE.pixels.floor[0] === "number") {
					// init min
					minleft = mapdata.IMAGE.pixels.floor[0] % mapdata.IMAGE.dimensions.width;
					mintop = mapdata.IMAGE.dimensions.height - 1 - Math.floor(mapdata.IMAGE.pixels.floor[0] / mapdata.IMAGE.dimensions.width);

					["floor", "obstacle"].forEach((key) => {
						ctx.beginPath();
						mapdata.IMAGE.pixels[key].forEach((px) => {
							if (key === "obstacle") {
								ctx.fillStyle = colors.newmap ? orgcolors[4] : colors.obstacle;
							} else {
								ctx.fillStyle = colors.newmap ? orgcolors[5] : colors.floor;
							}
							//ctx.fillStyle = colors[key];
							ctx.rect(this.getX(mapdata.IMAGE.dimensions, px), this.getY(mapdata.IMAGE.dimensions, px), this.adapter.config.map_scale, this.adapter.config.map_scale);

							maxtop = Math.max(maxtop, this.getY(mapdata.IMAGE.dimensions, px));
							maxleft = Math.max(maxleft, this.getX(mapdata.IMAGE.dimensions, px));
							minleft = Math.min(minleft, this.getX(mapdata.IMAGE.dimensions, px));
							mintop = Math.min(mintop, this.getY(mapdata.IMAGE.dimensions, px));
						});
						ctx.fill();
					});
				}
			}

			const segmentsData = {};
			const assignedColors: string[] = [];
			if (mapdata.IMAGE.pixels.segments && !mapdata.CURRENTLY_CLEANED_BLOCKS && colors.newmap) {
				const availableColors = [...orgcolors.slice(0, 4)]; // Exclude reserved colors

				// --- Collect segment data ---
				mapdata.IMAGE.pixels.segments.forEach((px) => {
					const segnum = px >> 21;
					const x = this.getX(mapdata.IMAGE.dimensions, px & 0xfffff);
					const y = this.getY(mapdata.IMAGE.dimensions, px & 0xfffff);

					if (!segmentsData[segnum]) {
						segmentsData[segnum] = { points: [], minX: x, maxX: x, minY: y, maxY: y };
					}
					const segment = segmentsData[segnum];
					segment.points.push({ x, y });
					segment.minX = Math.min(segment.minX, x);
					segment.maxX = Math.max(segment.maxX, x);
					segment.minY = Math.min(segment.minY, y);
					segment.maxY = Math.max(segment.maxY, y);
				});

				const segmentNums = Object.keys(segmentsData)
					.map(Number)
					.filter((n) => !isNaN(n));

				// Build adjacency matrix for the segments based on pixel data
				const adjacencyMatrix = this.buildAdjacencyMatrix(mapdata.IMAGE.pixels.segments, mapdata.IMAGE.dimensions.width, mapdata.IMAGE.dimensions.height);

				// Count the number of neighbors for each segment
				const neighborCounts = segmentNums.map((segnum) => {
					const neighbors = segmentNums.filter((otherSegnum) => segnum !== otherSegnum && adjacencyMatrix[segnum][otherSegnum]);
					return { segnum, count: neighbors.length };
				});
				neighborCounts.sort((a, b) => b.count - a.count);

				// --- Dynamically reserve special colors ---
				const maxAdjacentRoom = neighborCounts[0].segnum;
				const largestSegment = mapdata.IMAGE.segments.largestSegment;

				// Determine reserved colors:
				let reservedColors: string[] = [];
				if (maxAdjacentRoom === largestSegment) {
					// Reserve only one color, rest available for normal rooms
					assignedColors[maxAdjacentRoom] = orgcolors[1];
					reservedColors = [orgcolors[1]];
				} else {
					// Reserve two colors
					assignedColors[maxAdjacentRoom] = orgcolors[0];
					assignedColors[largestSegment] = orgcolors[1];
					reservedColors = [orgcolors[0], orgcolors[1]];
				}

				// List of available colors for other rooms:
				const restColors = availableColors.filter((c) => !reservedColors.includes(c));

				// --- Assign colors to all remaining segments alternately ---
				let colorIdx = 0;
				for (const { segnum } of neighborCounts) {
					if (assignedColors[segnum]) continue;
					assignedColors[segnum] = restColors[colorIdx % restColors.length];
					colorIdx++;
				}

				// --- Drawing as usual ---
				Object.keys(segmentsData).forEach((segnum) => {
					const segment = segmentsData[segnum];
					ctx.fillStyle = assignedColors[segnum] || "#CCCCCC";
					ctx.beginPath();
					segment.points.forEach((point) => {
						ctx.rect(point.x, point.y, this.adapter.config.map_scale, this.adapter.config.map_scale);
					});
					ctx.fill();
				});
				ctx.closePath();

				for (const { segnum } of neighborCounts) {
					if (assignedColors[segnum]) {
						continue;
					}
					const assignedColor = restColors[colorIdx % restColors.length];
					assignedColors[segnum] = assignedColor;
					colorIdx++;
				}
			}

			if (mapdata.CURRENTLY_CLEANED_BLOCKS && colors.newmap) {
				let segnum, lastcolor;
				ctx.beginPath();
				mapdata.IMAGE.pixels.segments.forEach((px) => {
					segnum = px >> 21;
					if (mapdata.CURRENTLY_CLEANED_BLOCKS.includes(segnum)) {
						if (segnum !== lastcolor) {
							ctx.fill();
							ctx.beginPath();
							ctx.fillStyle = orgcolors[segnum];
							lastcolor = segnum;
						}
						px = px & 0xfffff;
						ctx.rect(this.getX(mapdata.IMAGE.dimensions, px), this.getY(mapdata.IMAGE.dimensions, px), this.adapter.config.map_scale, this.adapter.config.map_scale);
					}
				});
				ctx.fill();
				ctx.closePath();
			}

			// Zeichne Zonen active Zonen
			if (mapdata.CURRENTLY_CLEANED_ZONES) {
				if (mapdata.CURRENTLY_CLEANED_ZONES[0]) {
					ctx.beginPath();
					mapdata.CURRENTLY_CLEANED_ZONES.forEach((coord) => {
						ctx.fillStyle = "rgba(46,139,87,0.1)";
						ctx.fillRect(
							this.robotXtoPixelX(mapdata.IMAGE, coord[0] / 50),
							this.robotYtoPixelY(mapdata.IMAGE, coord[1] / 50),
							this.robotXtoPixelX(mapdata.IMAGE, coord[2] / 50) - this.robotXtoPixelX(mapdata.IMAGE, coord[0] / 50),
							this.robotYtoPixelY(mapdata.IMAGE, coord[3] / 50) - this.robotYtoPixelY(mapdata.IMAGE, coord[1] / 50)
						);
						ctx.strokeStyle = "#2e8b57";
						ctx.lineWidth = 4;
						ctx.strokeRect(
							this.robotXtoPixelX(mapdata.IMAGE, coord[0] / 50),
							this.robotYtoPixelY(mapdata.IMAGE, coord[1] / 50),
							this.robotXtoPixelX(mapdata.IMAGE, coord[2] / 50) - this.robotXtoPixelX(mapdata.IMAGE, coord[0] / 50),
							this.robotYtoPixelY(mapdata.IMAGE, coord[3] / 50) - this.robotYtoPixelY(mapdata.IMAGE, coord[1] / 50)
						);
					});
				}
			}

			// Zeichne Teppich
			if (mapdata.CARPET_MAP) {
				const offset = 8 * this.adapter.config.map_scale;
				ctx.fillStyle = "rgba(0,0,0,0.5)";

				const imageData = ctx.getImageData(0, 0, mapdata.IMAGE.dimensions.width, mapdata.IMAGE.dimensions.height);
				mapdata.CARPET_MAP.forEach((px) => {
					const x2 = this.getX(mapdata.IMAGE.dimensions, px) - offset;
					const y1 = this.getY(mapdata.IMAGE.dimensions, px);
					const x1 = x2 + this.adapter.config.map_scale - 1;
					const y2 = y1 + this.adapter.config.map_scale - 1;

					this.drawLineBresenham(imageData, x1, y1, x2, y2);
				});

				// Zeichne das ganze ImageData auf einmal
				ctx.putImageData(imageData, 0, 0);
			}

			// Male den Wischpfad
			if (mapdata.PATH && mapdata.MOP_PATH) {
				const mopOffset = -12;
				const points = mapdata.PATH.points;
				const mops = mapdata.MOP_PATH;
				const scale = this.adapter.config.map_scale;
				ctx.lineWidth = 7 * scale;
				ctx.lineCap = "round";
				ctx.lineJoin = "round";
				ctx.strokeStyle = "rgba(255,255,255,0.18)"; // transparent, Roborock-like

				let lastIdx = -1;
				let segmentStarted = false;

				for (let i = 0; i < points.length; i++) {
					const mopIdx = i + mopOffset;
					if (mops?.[mopIdx] & 1) {
						const x = this.robotXtoPixelX(mapdata.IMAGE, points[i][0] / 50);
						const y = this.robotYtoPixelY(mapdata.IMAGE, points[i][1] / 50);

						if (!segmentStarted) {
							ctx.beginPath();
							ctx.moveTo(x, y);
							segmentStarted = true;
						} else {
							const last = points[lastIdx];
							const lx = this.robotXtoPixelX(mapdata.IMAGE, last[0] / 50);
							const ly = this.robotYtoPixelY(mapdata.IMAGE, last[1] / 50);
							const dx = x - lx;
							const dy = y - ly;
							const dist2 = dx * dx + dy * dy;
							const threshold = 10 * scale;
							if (dist2 > threshold * threshold || i !== lastIdx + 1) {
								// Start new segment
								ctx.stroke();
								ctx.beginPath();
								ctx.moveTo(x, y);
							} else {
								ctx.lineTo(x, y);
							}
						}
						lastIdx = i;
					} else if (segmentStarted) {
						// End of current segment: only stroke here!
						ctx.stroke();
						segmentStarted = false;
					}
				}
				// Stroke in case the last segment runs until the end
				if (segmentStarted) ctx.stroke();
			}

			// Male den Pfad
			if (mapdata.PATH?.points?.length) {
				ctx.fillStyle = colors.path;
				ctx.lineWidth = this.adapter.config.map_scale / 2;
				ctx.strokeStyle = colors.path;

				ctx.beginPath();
				let [cold1, cold2] = [this.robotXtoPixelX(mapdata.IMAGE, mapdata.PATH.points[0][0] / 50), this.robotYtoPixelY(mapdata.IMAGE, mapdata.PATH.points[0][1] / 50)];
				ctx.fillRect(cold1, cold2, (1 * this.adapter.config.map_scale) / 2, (1 * this.adapter.config.map_scale) / 2);

				mapdata.PATH.points.slice(1).forEach((coord) => {
					ctx.moveTo(cold1, cold2);
					cold1 = this.robotXtoPixelX(mapdata.IMAGE, coord[0] / 50);
					cold2 = this.robotYtoPixelY(mapdata.IMAGE, coord[1] / 50);
					ctx.lineTo(cold1, cold2);
				});

				ctx.stroke();
				ctx.closePath();
			}

			// Male geplanten Pfad
			if (mapdata.GOTO_PREDICTED_PATH) {
				if (mapdata.GOTO_PREDICTED_PATH.points && mapdata.GOTO_PREDICTED_PATH.points.length !== 0) {
					let cold1, cold2;
					ctx.lineWidth = (3 * this.adapter.config.map_scale) / 2;
					ctx.strokeStyle = "rgba(255, 255, 255, 1)";
					ctx.setLineDash([3 * this.adapter.config.map_scale, 3 * this.adapter.config.map_scale]);
					ctx.lineCap = "round";
					ctx.beginPath();
					mapdata.GOTO_PREDICTED_PATH.points.forEach((coord, index) => {
						if (index === 0) {
							cold1 = this.robotXtoPixelX(mapdata.IMAGE, coord[0] / 50);
							cold2 = this.robotYtoPixelY(mapdata.IMAGE, coord[1] / 50);
							ctx.fillStyle = "rgba(255, 255, 255, 1)";
							ctx.fillRect(cold1, cold2, (1 * this.adapter.config.map_scale) / 2, (1 * this.adapter.config.map_scale) / 2);
							ctx.moveTo(cold1, cold2);
						} else {
							const newCold1 = this.robotXtoPixelX(mapdata.IMAGE, coord[0] / 50);
							const newCold2 = this.robotYtoPixelY(mapdata.IMAGE, coord[1] / 50);
							if (cold1 !== newCold1 || cold2 !== newCold2) {
								ctx.lineTo(newCold1, newCold2);
								cold1 = newCold1;
								cold2 = newCold2;
							}
						}
					});
					ctx.stroke();
					ctx.setLineDash([]);
					ctx.lineCap = "butt";
				}
			}

			if (mapdata.FORBIDDEN_ZONES) {
				mapdata.FORBIDDEN_ZONES.forEach((zone) => {
					const forbiddenMinX = Math.min(zone[0], zone[2], zone[4], zone[6]);
					const forbiddenMinY = Math.min(zone[1], zone[3], zone[5], zone[7]);
					const forbiddenMaxX = Math.max(zone[0], zone[2], zone[4], zone[6]);
					const forbiddenMaxY = Math.max(zone[1], zone[3], zone[5], zone[7]);

					const forbiddenSizeX = forbiddenMaxX - forbiddenMinX;
					const forbiddenSizeY = forbiddenMaxY - forbiddenMinY;

					ctx.fillStyle = "rgba(255, 0, 0, 0.5)";
					ctx.fillRect(
						this.robotXtoPixelX(mapdata.IMAGE, forbiddenMinX / 50),
						this.robotYtoPixelY(mapdata.IMAGE, forbiddenMaxY / 50),
						(forbiddenSizeX / 50) * this.adapter.config.map_scale,
						(forbiddenSizeY / 50) * this.adapter.config.map_scale
					);
					ctx.lineWidth = (1 * this.adapter.config.map_scale) / 2;
					ctx.strokeStyle = "rgba(255, 0, 0, 1)";
					ctx.strokeRect(
						this.robotXtoPixelX(mapdata.IMAGE, forbiddenMinX / 50),
						this.robotYtoPixelY(mapdata.IMAGE, forbiddenMaxY / 50),
						(forbiddenSizeX / 50) * this.adapter.config.map_scale,
						(forbiddenSizeY / 50) * this.adapter.config.map_scale
					);
				});
			}

			if (mapdata.VIRTUAL_WALLS) {
				mapdata.VIRTUAL_WALLS.forEach((wall) => {
					const startX = this.robotXtoPixelX(mapdata.IMAGE, wall[0] / 50) + this.adapter.config.map_scale;
					const startY = this.robotYtoPixelY(mapdata.IMAGE, wall[1] / 50) + this.adapter.config.map_scale;
					const endX = this.robotXtoPixelX(mapdata.IMAGE, wall[2] / 50) + this.adapter.config.map_scale;
					const endY = this.robotYtoPixelY(mapdata.IMAGE, wall[3] / 50) + this.adapter.config.map_scale;

					// Calculate start end end of vector
					let vecX = endX - startX;
					let vecY = endY - startY;

					// Normalize vector
					const len = Math.sqrt(vecX * vecX + vecY * vecY);
					vecX /= len;
					vecY /= len;

					// Line width
					const lineWidth = 1 * this.adapter.config.map_scale;

					// Adjust start and end of the line
					const adjustedStartX = startX + vecX * (lineWidth / 2);
					const adjustedStartY = startY + vecY * (lineWidth / 2);
					const adjustedEndX = endX - vecX * (lineWidth / 2);
					const adjustedEndY = endY - vecY * (lineWidth / 2);

					ctx.lineWidth = lineWidth;
					ctx.strokeStyle = "rgba(255, 0, 0, 1)";

					ctx.beginPath();
					ctx.moveTo(adjustedStartX, adjustedStartY);
					ctx.lineTo(adjustedEndX, adjustedEndY);
					ctx.stroke();
				});
			}

			if (mapdata.NO_MOP_ZONE) {
				mapdata.NO_MOP_ZONE.forEach((zone) => {
					const noMopMinX = Math.min(zone[0], zone[2], zone[4], zone[6]);
					const noMopMinY = Math.min(zone[1], zone[3], zone[5], zone[7]);
					const noMopMaxX = Math.max(zone[0], zone[2], zone[4], zone[6]);
					const noMopMaxY = Math.max(zone[1], zone[3], zone[5], zone[7]);

					const noMopSizeX = noMopMaxX - noMopMinX;
					const noMopSizeY = noMopMaxY - noMopMinY;

					ctx.fillStyle = "rgba(0, 0, 255, 0.5)";
					ctx.fillRect(
						this.robotXtoPixelX(mapdata.IMAGE, noMopMinX / 50),
						this.robotYtoPixelY(mapdata.IMAGE, noMopMaxY / 50),
						(noMopSizeX / 50) * this.adapter.config.map_scale,
						(noMopSizeY / 50) * this.adapter.config.map_scale
					);
					ctx.lineWidth = (1 * this.adapter.config.map_scale) / 2;
					ctx.strokeStyle = "rgba(0, 0, 255, 1)";
					ctx.strokeRect(
						this.robotXtoPixelX(mapdata.IMAGE, noMopMinX / 50),
						this.robotYtoPixelY(mapdata.IMAGE, noMopMaxY / 50),
						(noMopSizeX / 50) * this.adapter.config.map_scale,
						(noMopSizeY / 50) * this.adapter.config.map_scale
					);
				});
			}

			if (mapdata.OBSTACLES2) {
				mapdata.OBSTACLES2.forEach((obstacle) => {
					const obstacleType = obstacle[2];
					const obstacleTitle = obstacleTitles[obstacleType] || "Unknown";
					const obstacleConfidence = Math.round(obstacle[3] / 100);
					const obstacleText = obstacleTitle[obstacleType] ? obstacleTitle + "(" + obstacleConfidence + "%)" : obstacleTitle;

					const x = this.robotXtoPixelX(mapdata.IMAGE, obstacle[0] / 50);
					const y = this.robotYtoPixelY(mapdata.IMAGE, obstacle[1] / 50);

					ctx.fillStyle = "red";
					ctx.beginPath();
					ctx.arc(x, y, 5, 0, 2 * Math.PI);
					ctx.fill();

					// Set the text properties
					ctx.font = "14px sans-serif";
					ctx.fillStyle = "white";
					ctx.textBaseline = "middle";
					ctx.textAlign = "center";

					// Calculate the text width and height
					const textWidth = ctx.measureText(obstacleText).width;
					const textHeight = parseInt(ctx.font, 10);

					// Calculate the position and dimensions of the background rectangle
					const padding = 5;
					const borderRadius = 5;
					const rectX = x - textWidth / 2 - padding;
					const rectY = y + 5 + padding / 2;
					const rectWidth = textWidth + 2 * padding;
					const rectHeight = textHeight + padding;

					// Draw the background rectangle with rounded corners
					ctx.fillStyle = "red";
					ctx.beginPath();
					ctx.moveTo(rectX + borderRadius, rectY);
					ctx.lineTo(rectX + rectWidth - borderRadius, rectY);
					ctx.quadraticCurveTo(rectX + rectWidth, rectY, rectX + rectWidth, rectY + borderRadius);
					ctx.lineTo(rectX + rectWidth, rectY + rectHeight - borderRadius);
					ctx.quadraticCurveTo(rectX + rectWidth, rectY + rectHeight, rectX + rectWidth - borderRadius, rectY + rectHeight);
					ctx.lineTo(rectX + borderRadius, rectY + rectHeight);
					ctx.quadraticCurveTo(rectX, rectY + rectHeight, rectX, rectY + rectHeight - borderRadius);
					ctx.lineTo(rectX, rectY + borderRadius);
					ctx.quadraticCurveTo(rectX, rectY, rectX + borderRadius, rectY);
					ctx.closePath();
					ctx.fill();

					// Draw the white text centered within the background rectangle
					ctx.fillStyle = "white";
					ctx.fillText(obstacleText, x, y + 5 + padding + textHeight / 2);
				});
			}

			// Zeichne Ladestation wenn vorhanden
			// if (mapdata.CHARGER_LOCATION) {
			// 	if (mapdata.CHARGER_LOCATION.position[0] && mapdata.CHARGER_LOCATION.position[1]) {
			// 		ctx.beginPath();
			// 		const img_charger_rotated = this.rotateCanvas(img_charger, mapdata.CHARGER_LOCATION.angle);
			// 		ctx.drawImage(
			// 			img_charger_rotated,
			// 			this.robotXtoPixelX(mapdata.IMAGE, mapdata.CHARGER_LOCATION.position[0] / 50) - img_charger_rotated.width / 2,
			// 			this.robotYtoPixelY(mapdata.IMAGE, mapdata.CHARGER_LOCATION.position[1] / 50) - img_charger_rotated.height / 2,
			// 			img_charger_rotated.width * 100,
			// 			img_charger_rotated.height * 100
			// 		);
			// 	}
			// }

			// Zeichne Roboter
			if (mapdata.ROBOT_POSITION) {
				if (mapdata.PATH.current_angle && mapdata.ROBOT_POSITION[0] && mapdata.ROBOT_POSITION[1]) {
					ctx.beginPath();
					const canvasImg = this.rotateCanvas(img, mapdata.PATH.current_angle);
					ctx.drawImage(
						canvasImg,
						this.robotXtoPixelX(mapdata.IMAGE, mapdata.ROBOT_POSITION.position[0] / 50) - img.width / 4,
						this.robotYtoPixelY(mapdata.IMAGE, mapdata.ROBOT_POSITION.position[1] / 50) - img.height / 4,
						canvasImg.width / 2,
						canvasImg.height / 2
					);
				} else {
					const img_robot_rotated = this.rotateCanvas(img, mapdata.ROBOT_POSITION.angle);
					ctx.drawImage(
						img_robot_rotated,
						this.robotXtoPixelX(mapdata.IMAGE, mapdata.ROBOT_POSITION.position[0] / 50) - img_robot_rotated.width / 4,
						this.robotYtoPixelY(mapdata.IMAGE, mapdata.ROBOT_POSITION.position[1] / 50) - img_robot_rotated.height / 4,
						img_robot_rotated.width / 2,
						img_robot_rotated.height / 2
					);
				}
			}

			// Zeichne Zielposition wenn vorhanden
			if (mapdata.GOTO_TARGET) {
				const go_to_pin_img = await loadImage(go_to_pin);

				if (mapdata.GOTO_TARGET[0] && mapdata.GOTO_TARGET[1]) {
					ctx.beginPath();
					ctx.drawImage(
						go_to_pin_img,
						this.robotXtoPixelX(mapdata.IMAGE, mapdata.GOTO_TARGET[0] / 50) - go_to_pin_img.width / 2,
						this.robotYtoPixelY(mapdata.IMAGE, mapdata.GOTO_TARGET[1] / 50) - (go_to_pin_img.height + 6),
						go_to_pin_img.width,
						go_to_pin_img.height
					);
				}
			}

			// Draw room names with background
			ctx.beginPath();

			// Get only rooms with valid names
			const roomIDsAll = this.adapter.http_api.getMatchedRoomIDs(false);

			Object.keys(segmentsData).forEach((segnum) => {
				if (parseInt(segnum) === 0) return; // Skip non-room area

				const segment = segmentsData[segnum];
				let roomName = "";

				if (typeof selectedMap !== "undefined" && mappedRooms) {
					const mapping = mappedRooms.find(([segmentID]) => parseInt(segmentID) === parseInt(segnum));

					if (mapping) {
						const roomID = mapping[1];
						const roomObj = roomIDsAll.find((r) => String(r.id) === String(roomID));
						roomName = roomObj?.name || "";

						if (!roomName) {
							this.adapter.log.debug(`Segment ${segnum} mapped to roomID ${roomID}, but no name found`);
						}
					}

					if (roomName) {
						const centerX = segment.minX + (segment.maxX - segment.minX) / 2;
						const centerY = segment.minY + (segment.maxY - segment.minY) / 2;

						ctx.font = `bold ${this.adapter.config.map_scale * 6}px Arial`;
						ctx.textAlign = "center";
						ctx.textBaseline = "middle";

						// Draw white outline (thin stroke)
						ctx.lineWidth = 1;
						ctx.strokeStyle = "white";
						ctx.strokeText(roomName, centerX, centerY);

						// Draw black text
						ctx.fillStyle = "black";
						ctx.fillText(roomName, centerX, centerY);
					}
				}
			});

			ctx.closePath();

			// crop image
			const canvas_trimmed = createCanvas(maxleft - minleft + 2 * offset, maxtop - mintop + 2 * offset);
			const ctx_trimmed = canvas_trimmed.getContext("2d");
			const trimmed = ctx.getImageData(minleft - offset, mintop - offset, maxleft - minleft + 2 * offset, maxtop - mintop + offset);

			ctx_trimmed.putImageData(trimmed, 0, 0);

			return [canvas.toDataURL(), canvas_trimmed.toDataURL()];
		} else {
			return [createCanvas(1, 1).toDataURL(), createCanvas(1, 1).toDataURL()]; // return empty canvas
		}
	}
}